import { Validators, createFormValidation } from '@lemoncode/fonk';
import { iban } from '@lemoncode/fonk-iban-validator';
import { rangeNumber } from '@lemoncode/fonk-range-number-validator';



// Se instala e importa librera de terceros para validad Iban y amount

/* Declaramos cul ser nuestro esquema de validacin. En "Field" se mencionar 
qu campos sern validados y qu tipos de validacin se realizara.
*/

const validationSchema = {
    field: {
      
        iban: [
        {
        validator: Validators.required,
        message: 'Campo requerido',
        },
        
        {
        validator: iban.validator,
        message: 'El campo debebe tener un formto vlido de IBAN',
        },
        ],
        
        name: [
        {
        validator: Validators.required,
        message: 'Campo requerido',
        },
        {
        validator: Validators.minLength,
        customArgs: { length: 10 }, 
        message: 'Debe ingresar {{length}} caracteres como mnimo',
        },
        ],

        amount:[
            {
            validator: Validators.required,
            message: 'Campo requerido',
            },

            {
            validator: rangeNumber.validator,
            message: 'Solo puedes transferir entre 10  y 500  ',
                customArgs: {
                        min: {
                        value: 10,
                        inclusive: true,
                    },
                        max: {
                        value: 500,
                        inclusive: true,
                    },
                }
                
            }
        ],
        
        concept:[
            {
            validator: Validators.required,
            message: 'Campo requerido',
            },
            {
            validator: Validators.minLength,
            customArgs: { length: 5 }, 
            message: 'Debe ingresar {{length}} caracteres como mnimo',
            },
        ],

        day:[ 
            {
            validator: Validators.required,
            message: 'Campo requerido',
            },

            {
            validator: rangeNumber.validator,
            message: 'Ingrese un nmero vlido para sealar el da',
                customArgs: {
                        min: {
                        value: 1,
                        inclusive: true,
                    },
                        max: {
                        value: 31,
                        inclusive: true,
                    },
                }
                
            }    
        ],

        month:[ 
            {
            validator: Validators.required,
            message: 'Campo requerido',
            },

            {
            validator: rangeNumber.validator,
            message: 'Ingrese un nmero vlido para sealar el mes',
                customArgs: {
                        min: {
                        value: 1,
                        inclusive: true,
                    },
                        max: {
                        value: 12,
                        inclusive: true,
                    },
                }
                
            }    
        ],

        year:[ 
            {
            validator: Validators.required,
            message: 'Campo requerido',
            },

            {
            validator: rangeNumber.validator,
            message: 'Solo est permitido programar transferencias durante el 2023',
                customArgs: {
                        min: {
                        value: 2023,
                        inclusive: true,
                    },
                        max: {
                        value: 2023,
                        inclusive: true,
                    },
                }
                
            }    
        ],

        email: [
            {
            validator: Validators.required,
            message: 'Campo requerido',
            },
            {
            validator: Validators.email,
            message: 'Email no vlido',
            },
        ],
    },
  }

  export const formValidation = createFormValidation(validationSchema);
